<template>
<div>
    <el-form ref="alconForm" :model="auditForm" :rules="rules" label-position="left" label-width="100px">
        <el-tag>分配会签人员</el-tag>
        <el-transfer
            filterable
            :filter-method="filterMethod"
            :titles="['待分配人员列表', '已分配人员列表']"
            filter-placeholder="请输入人员拼音"
            v-model="alconForm.countersign"
            :data="data">
        </el-transfer>

        <el-tag>分配审批人员</el-tag>
        <el-transfer
            filterable
            :filter-method="filterMethod"
            :titles="['待分配人员列表', '已分配人员列表']"
            filter-placeholder="请输入人员拼音"
            v-model="alconForm.audit"
            :data="data">
        </el-transfer>

        <el-tag>分配签订人员</el-tag>
        <el-transfer
            filterable
            :filter-method="filterMethod"
            :titles="['待分配人员列表', '已分配人员列表']"
            filter-placeholder="请输入人员拼音"
            v-model="alconForm.sign"
            :data="data">
        </el-transfer>

         <el-form-item>
                    <el-row>
                        <el-col :span="15">
                            <el-button type="primary" @click="postContract">确定</el-button>
                        </el-col>

                        <el-col :span="9">
                            <el-button type="danger" @click="resetContract">重置</el-button>
                        </el-col>
                    </el-row>
                </el-form-item>
    </el-form>
</div>
</template>

<script>
  export default {
    name:"editallocate_contract",
    data() {
      const generateData = a => {
        const data = [];
        const cities = [];
        const pinyin = [];
        cities.forEach((city, index) => {
          data.push({
            label: a,
            key: index,
            pinyin: pinyin[index]
          });
        });
        return data;
      };
      return {
        data: generateData(),
        value: [],
        filterMethod(query, item) {
          return item.pinyin.indexOf(query) > -1;
        }
      };
    },
    methods :{
        resetContract() {
                this.$confirm("此操作将清空当前分配内容，是否继续？", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning"
                }).then(() => {
                    this.finalForm = this.cleanJson(this.finalForm);
                    this.$notify({
                        title: '成功',
                        message: '清空成功！',
                        type: "success"
                    })
                })
            },
            postContract() {
                 this.$refs["alconForm"].validate((valid) => {
                    if (valid) {
                        
                        this.$axios({
                            url: "/contract/allocate",
                            method: 'post',
                            data: {
                                name: this.finalForm.name,
                                userName: this.finalForm.userName,
                                dataFrom: this.finalForm.dataFrom,
                                dataTo: this.finalForm.dataTo,
                                info: this.finalForm.info
                            },
                            transformRequest: [function (data) {
                                let ret = '';
                                for (let it in data) {
                                    ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
                                }
                                return ret
                            }],
                            headers: {'Content-Type': 'application/x-www-form-urlencoded'}
                        }).then(res => {
                            // eslint-disable-next-line no-console
                            console.log(res);
                        })
                    } else {
                        this.$notify({
                            title: '失败',
                            message: '提交失败！',
                            type: "error"
                        })
                    }
                })
            },
            cleanJson(json) {
                for (let finalFormKey in json) {
                    json[finalFormKey] = ''
                }
                return json;
            }
        }
    
  };
</script>